.theme-dark {
   --font-color: #ffffff;
   --equal-font-color: #ffffff;
   --small-buttons-color: #181f33;
   --choise-circle-color: #d03f2f;
   --partial-backgrounf-color: #242d44;
   --minor-background-color: #181f33;
   --core-background-color: #3a4663;
   --small-buttons-background: #eae3dc;
   --text-buttons-background: #647198;
   --equal-button-background: #d03f2f;
   --small-buttons-background-hover: #ffffff;
   --text-buttons-background-hover: #a2b2e1;
   --equal-button-background-hover: #f96b5b;
   --small-buttons-shadow: inset 0px -4px 0px #b3a497;
   --text-buttons-shadow: inset 0px -4px 0px #414e73;
   --equal-button-shadow: inset 0px -4px 0px #93261a;
}

.theme-light {
   --font-color: #36362c;
   --equal-font-color: #ffffff;
   --small-buttons-color: #181f33;
   --choise-circle-color: #d03f2f;
   --partial-backgrounf-color: #d2cdcd;
   --minor-background-color: #eeeeee;
   --core-background-color: #e5e4e1;
   --small-buttons-background: #eae3dc;
   --text-buttons-background: #378187;
   --equal-button-background: #c85402;
   --small-buttons-background-hover: #ffffff;
   --text-buttons-background-hover: #62b5bc;
   --equal-button-background-hover: #ff8a38;
   --small-buttons-shadow: inset 0px -4px 0px #b3a497;
   --text-buttons-shadow: inset 0px -4px 0px #414e73;
   --equal-button-shadow: inset 0px -4px 0px #93261a;
}

.theme-toxic {
   --font-color: #ffe53d;
   --equal-font-color: #1a2327;
   --small-buttons-color: #ffe53d;
   --choise-circle-color: #93fff8;
   --partial-backgrounf-color: #1e0936;
   --minor-background-color: #1e0936;
   --core-background-color: #17062a;
   --small-buttons-background: #331c4d;
   --text-buttons-background: #56077c;
   --equal-button-background: #00ded0;
   --small-buttons-background-hover: #6c34ac;
   --text-buttons-background-hover: #8631af;
   --equal-button-background-hover: #93fff8;
   --small-buttons-shadow: inset 0px -4px 0px #881c9e;
